import { PropsWithChildren, useState } from "react"
import { useElementSize } from "@rp/react/web/common/hooks"
import { Box, SxProps } from "@rp/react/web/mui/core"

type Props = PropsWithChildren & {
  margin?: number
  duration?: number
  minHeight?: number
  id?: string
  sx?: SxProps
}

const AutoAnimateHeight = ({
  children,
  duration = 0.3,
  margin = 0,
  minHeight = 0,
  id,
  sx,
}: Props) => {
  const [wrapper, setWrapper] = useState<HTMLDivElement | null>(null)
  const { height } = useElementSize(wrapper)

  return (
    <Box
      sx={{
        height: Math.max(height + margin, minHeight),
        transition: `height ${duration}s`,
        overflow: "hidden",
        ...sx,
      }}
      id={id}
    >
      <div ref={setWrapper}>{children}</div>
    </Box>
  )
}

export { AutoAnimateHeight }
